@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed:ital,wght@1,300&display=swap);
body {
  margin: 0;
  padding: 0;
  overflow: hidden;
  overflow-y: hidden;
  font-family: 'Open Sans Condensed', sans-serif; }

.video {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%; }

.img {
  top: 0;
  left: 0;
  position: absolute;
  height: 100%;
  width: 100%;
  background-size: 130%;
  -webkit-animation-name: smooth2;
          animation-name: smooth2;
  -webkit-animation-duration: 30s;
          animation-duration: 30s;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite; }

.img1 {
  background-image: url("https://images.pexels.com/photos/34592/pexels-photo.jpg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); }

.img2 {
  background-image: url("https://images.pexels.com/photos/185762/pexels-photo-185762.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); }

.img3 {
  background-image: url("https://images.pexels.com/photos/1112048/pexels-photo-1112048.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); }

.img4 {
  background-image: url("https://images.pexels.com/photos/904616/pexels-photo-904616.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); }

.img5 {
  background-image: url("https://images.pexels.com/photos/214659/pexels-photo-214659.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=650&w=940"); }

.buttons-list {
  position: absolute;
  bottom: 20px;
  left: 0;
  z-index: 10;
  width: 100%;
  display: flex;
  justify-content: center;
  opacity: 0.3;
  -webkit-transition: 0.3s;
  transition: 0.3s; }

.buttons-list:hover {
  opacity: 0.8; }

.btn {
  padding: 10px 50px;
  margin: 0 10px;
  background-color: lightgray;
  cursor: pointer;
  -webkit-transition: 0.3s;
  transition: 0.3s;
  border: 0;
  outline: none; }

.btn:hover {
  box-shadow: 0 0 5px lightgray, 0 0 25px lightgray, 0 0 50px lightgray, 0 0 200px lightgray; }

.sign1 {
  position: absolute;
  top: 60px;
  left: 80px;
  color: white;
  z-index: 20;
  font-size: 40px;
  opacity: 0;
  -webkit-animation-name: sign1;
          animation-name: sign1;
  -webkit-animation-duration: 17s;
          animation-duration: 17s;
  -webkit-animation-delay: 10s;
          animation-delay: 10s; }

.sign2 {
  position: absolute;
  color: white;
  z-index: 20;
  font-size: 40px;
  top: 500px;
  right: 80px;
  opacity: 0;
  -webkit-animation-name: sign1;
          animation-name: sign1;
  -webkit-animation-duration: 17s;
          animation-duration: 17s;
  -webkit-animation-delay: 30s;
          animation-delay: 30s; }

.sign3 {
  position: absolute;
  top: 300px;
  left: 500px;
  color: white;
  z-index: 20;
  font-size: 40px;
  opacity: 0;
  -webkit-animation-name: sign1;
          animation-name: sign1;
  -webkit-animation-duration: 17s;
          animation-duration: 17s;
  -webkit-animation-delay: 50s;
          animation-delay: 50s; }

.sign4 {
  position: absolute;
  top: 300px;
  left: 500px;
  color: white;
  z-index: 20;
  font-size: 40px;
  opacity: 0;
  -webkit-animation-name: sign4;
          animation-name: sign4;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-delay: 69s;
          animation-delay: 69s; }

@-webkit-keyframes sign1 {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes sign1 {
  0% {
    opacity: 0; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes sign4 {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@keyframes sign4 {
  0% {
    opacity: 1; }
  50% {
    opacity: 1; }
  100% {
    opacity: 0; } }

@-webkit-keyframes smooth2 {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

@keyframes smooth2 {
  0% {
    background-position: 0% 0%; }
  25% {
    background-position: 40% 10%; }
  50% {
    background-position: 0 10%; }
  75% {
    background-position: 10% 40%; }
  100% {
    background-position: 0% 0%; } }

